@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
.nothingfound {
  width: 50%;
  margin: 0 40%;
  font-size: 20px;
  font-weight: 700;
}
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}
.Productdetails {
  width: 100%;
}
.dotcart {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 15px;
  font-weight: 800;
  position: absolute;
  top: 13px;
  color: #ebebeb;
  right: 130px;
  height: 25px;
  width: 25px;
  border-radius: 10px;
  background-color: #0071dc;
}
.loading {
  z-index: 20;
  text-align: center;
  background-color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
}

.logo {
  width: 36%;
  margin-left: 80px;
  margin-right: 80px;
}
nav {
  width: 100%;
  position: fixed;
  top: 0;
  height: 70px;
  align-items: center;
  font-size: 1.1em;
  font-weight: 600;
  display: flex;
  background-color: transparent;
  transition: all 0.5s linear;
  z-index: 1000;
}
.nav.active {
  background: white;
  box-shadow: 0 5px 5px -2px rgba(0, 0, 0, 0.4);
}
.activenav {
  border-bottom: 0.1px #cccccc solid;
}
.navLinks {
  width: 100%;
  background-color: transparent;
  list-style: none;
  justify-content: space-around;
  display: flex;
  font-weight: 600;
}
.navLinks li {
  font-size: 0.9em;

  cursor: pointer;
}
.navLinks li::after {
  content: "";
  display: block;
  width: 0px;
  height: 2px;
  background: #000000;
  transition: width 0.3s;
}
.activlink li::after {
  content: "";
  display: block;
  width: 0px;
  height: 2px;
  background: #000000;
  transition: width 0.3s;
  width: 100%;
}
.navLinks li:hover::after {
  width: 100%;
  transition: width 0.3s;
}
.search {
  display: none;
}
.iconCart,
.signin {
  cursor: pointer;
  font-size: 0.9em;
}
.rightComp {
  align-items: center;
  display: flex;
  justify-content: space-around;
  margin-left: 300px;
  margin-right: 20px;
}
.signin {
  width: 100px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 20px;
  color: #000000;
  border: 2px solid;
  transition: all 0.2s;
}
.iconSearch {
  margin-right: 20px;
  cursor: pointer;
}
.signin:hover {
  border-color: #000000;
  color: rgb(0, 0, 0);
  box-shadow: 0 0.5em 0.5em -0.4em #000000;
  -webkit-transform: translateY(-0.25em);
          transform: translateY(-0.25em);
}
.searchActive {
  display: flex;
  margin-left: 0;
  margin-right: 15px;
  opacity: 1;
  width: 200px;
}

.burger {
  display: none;
  cursor: pointer;
}
.burger div {
  border-radius: 99px;
  width: 30px;
  height: 4px;
  background-color: #000000;
  margin: 6px;
  transition: all 0.3s ease;
}
/*slider*/

.slider {
  position: relative;
  height: 700px;
}
.slide {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: all 0.4s ease-in-out;
}
.slide.current {
  display: inline;
  opacity: 1;
}
.ShopNow {
  width: 190px;
  align-items: center;
  border: none;
  background-color: #1e1e2c;
  color: rgb(241, 241, 241);
  border-radius: 30px;
  font-size: 19px;
  outline: none;
  padding: 11px 0px;
  transition: all ease-out 0.3s;
}
.ShopNow:hover {
  color: #1e1e2c;
  cursor: pointer;
  background-color: #ebebeb;
}
.ShopNow::before {
  position: absolute;
  top: 0;
  left: 50%;
  right: 50%;
  bottom: 0;
  opacity: 0;
  content: "";
  transition: all ease 0.5s;
}
.ShopNow:hover::before {
  transition: 0.5s all ease;
  border-radius: 30px;
  left: 0;
  right: 0;
  opacity: 1;
  z-index: -1;
  box-shadow: 0 5px 5px -2px rgba(0, 0, 0, 0.5);
}
/*btns */
.slide .content {
  opacity: 0;
  position: absolute;
  left: -600px;
  bottom: 70px;
}
.slide.current .content {
  z-index: 1;
  opacity: 1;
  -webkit-transform: translateX(700px);
          transform: translateX(700px);
  transition: all 0.7s ease-in-out 0.3s;
}

.slide .titleslider {
  opacity: 0;
  position: absolute;
  top: -600px;
  left: 100px;
  font-size: 70px;
  font-weight: 700;
}
.slide .subtitleslider {
  opacity: 0;
  position: absolute;
  top: -600px;
  left: 100px;
  font-size: 30px;
  font-weight: 500;
}
.slide.current .titleslider {
  opacity: 1;
  -webkit-transform: translateY(870px);
          transform: translateY(870px);
  transition: all 0.7s ease-in-out 0.3s;
}
.slide.current .subtitleslider {
  opacity: 1;
  -webkit-transform: translateY(970px);
          transform: translateY(970px);
  transition: all 0.7s ease-in-out 0.3s;
}
.next {
  position: absolute;
  opacity: 0.5;
  top: 50%;
  right: 15px;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}
.prev {
  position: absolute;
  opacity: 0.5;
  top: 50%;
  left: 15px;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}
.next:hover,
.prev:hover {
  opacity: 1;
}
/*background Images */

.slide:first-child {
  background: url("https://i.imgur.com/jJYOdBg.jpg") no-repeat center top/cover;
}
.slide:nth-child(2) {
  background: url("https://i.imgur.com/7rwaigw.jpg") no-repeat center top/cover;
}
.slide:nth-child(3) {
  background: url("https://i.imgur.com/3Obf6Tt.jpg") no-repeat center top/cover;
}
.slide:nth-child(4) {
  background: url("https://i.imgur.com/laeEnPm.jpg") no-repeat center top/cover;
}
/*/////////////Categories Cards ///////////////*/
.cards {
  display: grid;
  margin: 48px;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-column-gap: 50px;
  -webkit-column-gap: 50px;
          column-gap: 50px;
  grid-row-gap: 20px;
  row-gap: 20px;
}
.cardscg {
  width: 100%;
  height: 250px;
  display: grid;
  cursor: pointer;
  position: relative;
  box-shadow: 0 5px 5px -2px rgba(0, 0, 0, 0.2);
  grid-template-rows: 70% 30%;
}
.cardscg h1 {
  font-size: 30px;
  font-weight: 600;
  margin-left: 20px;
  margin-top: 20px;
}
.ShopNowcg {
  display: block;
  position: absolute;
  opacity: 0;
  top: 40px;
  left: 0;
  right: 0;
  bottom: 0;
  margin-left: 30px;
  font-weight: 600;
  grid-row: 2/2;
  outline-style: none;
}
.cardscg:hover .ShopNowcg {
  position: relative;
  transition: 0.5s all ease;
  opacity: 1;
  align-self: center; /*Vertical */
  justify-self: start; /*Horizontal */
  -webkit-transform: translateY(-50px);
          transform: translateY(-50px);
}
.cardscg:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  box-shadow: 0 10px 10px rgba(0, 0, 0, 0.5);
  transition: 0.5s all ease;
}
.cardscg:hover * {
  color: white;
  z-index: 3;
}
.cardscg::after {
  transition: 0.5s all ease;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 20px;
  left: 0;
  right: 0;
  bottom: 0;
  content: "";
  opacity: 0;
}
.cardscg:hover::after {
  display: block;
  transition: 0.5s all ease;
  -webkit-transform: translateY(-20px);
          transform: translateY(-20px);
  left: 0;
  right: 0;
  opacity: 0.9;
  z-index: 0;
  background: linear-gradient(180deg, rgb(75, 57, 116), rgba(0, 0, 0, 0.493));
}
.ShopNowcg::after {
  content: "";
  transition: all 1s ease-out;
  width: 0;
  height: 3px;
  position: absolute;
  left: 50%;
  right: 50%;
  top: 100%;
  background: white;
}
.cardscg:hover .ShopNowcg::after {
  transition: all 1s ease-out;
  width: 100%;
  left: 0;
  right: 0;
}
.cardscg:first-child {
  background: url("https://i.imgur.com/ugsxgsY.jpg") no-repeat center top/cover;
}
.cardscg:nth-child(2) {
  background: url("https://i.imgur.com/roN4Tqt.jpg") no-repeat center top/cover;
}
.cardscg:nth-child(3) {
  background: url(../../static/media/img1.fcaa3f12.jpg) no-repeat center top/cover;
}

/* --- CgDiv ----*/

.Cgfilter {
  margin: 35px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(60%, 1fr));
  grid-template-rows: repeat(auto-fit, 1fr);
  grid-row-gap: 8px;
  row-gap: 8px;
}
.Cgfilter h1 {
  width: 100%;
  align-self: center;
  font-size: 30px;
  font-weight: 700;
  font-style: bold;
  margin-left: 20px;
}
.filters {
  margin-top: 20px;
  grid-row: 2/2;
  justify-self: start;
  margin-left: 20px;
}
.filters ul {
  display: grid;
  width: 100%;
  grid-template-columns: repeat(6, minmax(60px, 90px));
  font-size: 15px;
  font-weight: 600;
}
.filtersbtn {
  align-self: center;
  justify-self: center;
  grid-row: 2/2;
  grid-column: 2/2;
}

.filterbtn,
.searchbtn {
  margin-right: 30px;
  padding: 10px 0;
  outline: none;
  min-width: 100px;
  height: 40px;
  cursor: pointer;
  color: #888;
  border: 1px solid #e6e6e6;
  border-radius: 3px;
  font-family: "Poppins-Regular", sans-serif;
  background-color: #f2f2f2;
  font-size: 15px;
  line-height: 1.2;
  transition: all 0.2s;
  align-items: center;
  justify-content: center;
}

.filterbtn > *,
.searchbtn > * {
  display: inline-block;
  margin-right: 3px;
}

.filterbtn:hover,
.searchbtn:hover {
  color: white;
  background-color: rgb(113, 127, 224);
}

.lined {
  color: #1e1e2c;
  opacity: 0.5;
  list-style: none;
  cursor: pointer;
  transition: all 0.3s ease-out;
}
.lined:hover,
.selected {
  opacity: 1;
}
.lined::after {
  content: "";
  display: block;
  width: 0;
  height: 2px;
  background: #1e1e2c;
  transition: width 0.3s;
}
.lined:hover::after {
  width: 60%;
  transition: width 0.3s;
}

/* --- Filtre ----*/

.filterarea {
  display: grid;
  height: 0;
  opacity: 0;
  margin: 35px;
  grid-template-columns: repeat(auto-fit, minmax(30%, 1fr));
  grid-template-rows: repeat(auto-fit, minmax(1fr, 50px));
  grid-row-gap: 8px;
  row-gap: 8px;
  background-color: #ebebeb;
  justify-items: center;
  margin-bottom: 20px;
}
.filterarea h1 {
  margin: 20px 0px;
  font-size: 18px;
  font-weight: 600;
}
.filterarea li {
  margin-top: 8px;
  list-style: none;
}
.filter {
  height: 300px;
  opacity: 1;
  transition: all 0.3s ease;
}
.filteroff {
  height: 0;
  opacity: 0;
  transition: all 0.3s ease;
}
